.catalog {
    padding: 86px 0 120px 0;

    @include vp-1023 {
        padding-top: 90px;
        padding-bottom: 100px;
    }

    @include vp-767 {
       padding-top: 57px;
       padding-bottom: 65px;
    }

    &__wrapper {
        display: flex;
        flex-direction: column;
    }

    &__title {
        margin: 0;
        margin-bottom: 48px;

        font-weight: 600;
        font-size: 28px;
        line-height: 42px;
        text-align: center;

        @include vp-1023 {
            font-size: 26px;
            line-height: 39px;
        }

        @include vp-767 {
            margin-bottom: 31px;

            font-size: 24px;
            line-height: 36px;
        }
    }

    &__list {
        margin: 0;
        padding: 0 15px;

        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 30px;

        list-style: none;

        font-size: 14px;
        line-height: 20px;
        color: $color-alice-blue;

        @include vp-1279 {
            grid-template-columns: repeat(3, 1fr);
        }

        @include vp-1023 {
            grid-template-columns: repeat(2, 1fr);
        }

        @include vp-767 {
            padding: 0 10px;

            grid-template-columns: 1fr;
        }
    }
}